// 
// const base_url = 'http://127.0.0.1:9999'
const base_url = 'https://wx.wlsx.fun:9999'
// 
const timeout = 5000
//
let flag=true
//
let isPop=true
// token
export default (params) => {
	let url = params.url;
	let type=params.type;
	let method = params.method || "get";
	let data = params.data || {};
	let header = {
		'Authorization': uni.getStorageSync('token') || '',
		'Content-Type': 'application/json;charset=UTF-8',
		// 'Tenant-Id': uni.getStorageSync('tenantId') || 'xxx', // avue
		...params.header
	}
	if (method == "post") {
		header = {
			'Content-Type': 'application/json',
			'Authorization': uni.getStorageSync('token') || '',
			...params.header
		};
	}
	return new Promise((resolve, reject) => {
		uni.request({
			url: base_url+url,
			method: method,
			header: header,
			data: data,
			timeout: timeout,
			success(response) {
				const res = response
				// 
				//
				if (res.statusCode == 200) {
					resolve(res.data)
				} else {
					uni.clearStorageSync()
					switch (res.statusCode) {
						case 401:
							uni.removeStorageSync('userInfo')
							uni.removeStorageSync('token')
							if(isPop){
								isPop=false
								uni.showModal({
									title: "",
									content: "",
									showCancel: false,
									success() {
										if(flag){
											setTimeout(() => {
												uni.navigateTo({
													url: "/pages/login/login",
												})
												flag=false
											}, 1000);
										}
									},
								});
							}
							break;
						case 404:
							uni.showToast({
								title: '...',
								duration: 2000,
							})
							break;
						default:
							uni.showToast({
								title: '...',
								duration: 2000,
							})
							break;
					}
				}
			},
			fail(err) {
				console.log(err)
				if (err.errMsg.indexOf('request:fail') !== -1) {
					uni.showToast({
						title: '',
						icon: "error",
						duration: 2000
					})
				} else {
					uni.showToast({
						title: '',
						duration: 2000
					})
				}
				reject(err);

			},
			complete() {
				// 
				uni.hideLoading();
				uni.hideToast();
			}
		});
	}).catch(() => {});
};
