// 
// const base_url = 'http://172.29.35.235:9999'
// 
const timeout = 5000
//
let flag = true
//
let isPop = true
// token
export default (params) => {
	let url = params.url;
	let type = params.type;
	let method = params.method || "get";
	let data = params.data || {};
	let header = {
		'Authorization': uni.getStorageSync('token') || '',
		'Content-Type': 'application/json;charset=UTF-8',
		// 'Tenant-Id': uni.getStorageSync('tenantId') || 'xxx', // avue
		...params.header
	}
	if (method == "post") {
		header = {
			'Content-Type': 'application/json',
			'Authorization': uni.getStorageSync('token') || '',
			...params.header
		};
	}
	return new Promise((resolve, reject) => {
		uni.request({
			url: url,
			method: method,
			header: header,
			data: data,
			timeout: timeout,
			success(response) {
				const res = response
				switch(res.code){
					case 10002:
						uni.showToast({
							title: '',
							icon: "error",
							duration: 2000
						})
						break
					case 40000:
						uni.showToast({
							title: '',
							icon: "error",
							duration: 2000
						})
						break
					case 40007:
						uni.showToast({
							title: '',
							icon: "error",
							duration: 2000
						})
						break
					case 10002:
						uni.showToast({
							title: '',
							icon: "error",
							duration: 2000
						})
						break
					default:
						resolve(res.data)
						break
				}
				
			},
			fail(err) {
				console.log(err)
				if (err.errMsg.indexOf('request:fail') !== -1) {
					uni.showToast({
						title: '',
						icon: "error",
						duration: 2000
					})
				} else {
					uni.showToast({
						title: '',
						duration: 2000
					})
				}
				reject(err);

			},
			complete() {
				// 
				uni.hideLoading();
				uni.hideToast();
			}
		});
	}).catch(() => {});
};
