export const bafaLogin = (params) => {
	return bafaRequest({
		url: 'https://pro.bemfa.com/v1/login',
		type:'other',
		method: 'post',
		data: params,
		header: {
			"Content-Type": "application/x-www-form-urlencoded"
		} // 
	})
}
